html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: #FEFAE0;
  background: var(--dark);
  color: #111111;
  color: var(--light);
    /* font-family: 'Poiret One', cursive; */
  font-family: Verdana, Geneva, Tahoma, sans-serif

}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #e36f52;
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
  }

a:hover {
  text-decoration: underline;
}

header {
  background: #FEFAE0;
  background: var(--dark);
}

header a {
  color: #111111;
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #F3A847;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
  padding: 0 0 0 40px;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #FEFAE0;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid #F3A847;
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

ul {
  padding: 0
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
  
}

nav  li {
