.grid-container {
  display: flex;
 
}

.grid-container-profile {
  display: grid;
  align-items: center;
  justify-items: left;
  grid-template-columns: 650px 150px;
  grid-template-rows: repeat(4,150px);
  margin: 1.5%;
}

.grid-item {
  
  position: relative;
}

.title-uni {
  font-family: Roboto;
  font-size: 18px;
}

.title-concentration {
  font-family: Roboto;
  font-size: 14px;
}

